.aside{position:fixed;top:100px;left:25px;width:120px;height:50rem;padding:2rem 0;opacity:1;transition:opacity 0.2s 0.5s ease}.aside--hidden{opacity:0;transition:opacity 0.2s ease}.aside__bar{width:2px;height:100%;background:#fff;opacity:0.3;position:absolute;top:10px;left:17px}.aside__list{height:100%;display:flex;flex-direction:column;justify-content:space-between}.aside__section{display:flex;justify-content:space-between;padding-left:30px;position:relative}.aside__section-index{position:absolute;left:30px;opacity:0.5;transition:transform .5s ease}.aside__section-name{opacity:0;text-transform:uppercase;letter-spacing:0.12em;transition:transform .5s .2s ease, opacity .5s .2s ease;transform:translateX(30px)}.aside__section--active .aside__section-index{opacity:1;transform:translateX(-40px)}.aside__section--active .aside__section-name{opacity:1;transform:translateX(0)}@media (max-width: 900px){.aside{display:none}}

.content{position:relative;width:100%;transition:transform 0.3s 0.1s ease, opacity 0.3s 0.1s ease;transform-origin:left}.content.moved{transition:transform 0.3s 0.1s ease, opacity 0.3s 0.1s ease;transform:translateX(-50%);opacity:0.5}@media (max-width: 450px){.content.moved{transform:translateX(-90%)}}

.header{color:#fff;margin:0 auto;display:flex;flex-direction:column;align-items:center;max-width:85%}.hero-wrapper{font-size:1.6rem;background-image:url(/portfolio/static/media/cosmonaut.bb026ce2.png);background-repeat:no-repeat;background-position:bottom 0 right 0;background-size:31em;width:100%}.hero{min-height:45rem;color:#fff;display:flex;width:85%;flex-direction:column;align-items:flex-start;justify-content:flex-end;margin:0 auto}.hero__title{font-size:4em;letter-spacing:0.05em;line-height:1;text-transform:uppercase;margin-bottom:6rem;max-width:6em}.hero__title span{display:block;font-size:0.4em;font-weight:400;text-transform:none;opacity:0.9}.hero__cta{color:#fff;background:transparent;border:none;outline:none;position:relative;height:50px;width:150px;font-size:2rem;display:flex;align-items:center;text-transform:uppercase;cursor:pointer;margin-bottom:10rem}.hero__cta:focus{border:2px solid #a34bed}.hero__cta img{height:24px}.hero__cta::before{content:'';position:absolute;top:0;left:0;height:100%;width:80%;transform:translateX(15px);background:#a34bed;z-index:-1;transition:-webkit-filter 0.2s ease;transition:filter 0.2s ease;transition:filter 0.2s ease, -webkit-filter 0.2s ease}.hero__cta:hover::before{-webkit-filter:saturate(2);filter:saturate(2)}.header__info{display:flex;flex-wrap:wrap;margin-bottom:50px;text-align:center}.header__info-item{padding:2rem;flex:1 1 250px;opacity:0.7;position:relative;border-top:4px solid #333}.header__info-item::after{content:'';position:absolute;top:-4px;left:0;height:4px;width:15%;background:#a34bed;opacity:0}.header__info-item:hover,.header__info-item:focus{opacity:1}.header__info-item:hover::after,.header__info-item:focus::after{opacity:1}.header__info-header{letter-spacing:0.05em;text-transform:uppercase;margin-bottom:24px}.header__info-text{line-height:1.4}@media (max-width: 1024px){.hero-wrapper{font-size:1.5rem}}@media (max-width: 980px){.hero-wrapper{font-size:1.4rem}}@media (max-width: 900px){.hero-wrapper{font-size:1.3rem}.header{max-width:100%}}@media (max-width: 850px){.hero-wrapper{background-size:25em}}@media (max-width: 800px){.hero{min-height:auto}.hero-wrapper{background-size:20em}}@media (max-width: 600px){.hero-wrapper{background-size:15em}.hero__title{font-size:3em}}

.loading{height:100vh;width:100%;position:absolute;top:0;left:0;display:flex;flex-direction:column;align-items:center;justify-content:center}.loading__spinner{pointer-events:none;width:10em;height:10em;border:1em solid transparent;border-color:#eee;border-top-color:#a34bed;border-radius:50%;-webkit-animation:spin 1s linear infinite;animation:spin 1s linear infinite;margin-top:4rem}.loading__title{text-transform:uppercase;letter-spacing:0.12em;margin-top:4rem;font-size:4rem;font-weight:bold}@-webkit-keyframes spin{100%{transform:rotate(360deg)}}@keyframes spin{100%{transform:rotate(360deg)}}

.main{width:100%}

.nav{display:flex;justify-content:space-between;align-items:center;height:8rem;background:#0c0c0c;padding:2rem 5rem;box-shadow:0 0 4px 0px #a34bed;position:fixed;top:0;left:0;z-index:100;width:100%}.nav__brand{display:flex;align-items:center}.nav__logo{height:3rem}.nav__dev{display:inline-block;text-transform:uppercase;font-weight:bold;margin-left:2rem;letter-spacing:0.05em}.nav__burger{width:40px;height:30px;display:flex;flex-direction:column;align-items:flex-end;justify-content:space-between;background:transparent;padding:4px;outline:none;border:none;cursor:pointer;position:relative}.nav__burger:focus{box-shadow:0 0 0 3px #a34bed}.nav__burger-part{width:100%;height:2px;background:#fff;transition:opacity 0.2s 0.2s ease, transform 0.2s 0.2s ease}.nav__burger-part:nth-child(2){transition:opacity 0.2s 0.2s ease, transform 0.2s 0.2s ease;width:80%}.nav__burger--open .nav__burger-part:first-child{transform:translateY(10px) rotate(45deg)}.nav__burger--open .nav__burger-part:nth-child(2){opacity:0;transition:opacity 0.2s ease, transform 0.2s ease;transform:translateX(200%)}.nav__burger--open .nav__burger-part:last-child{transform:translateY(-10px) rotate(-45deg)}.nav__list{font-size:1.6rem;position:absolute;padding:20px 50px;right:0;top:100%;max-width:100%;list-style:none;opacity:1;transition:opacity 0.1s ease-in-out, transform 0.1s ease-in-out;transform:scaleY(0);transform-origin:top}.nav__list--active{opacity:1;transition:opacity 0.3s 0.1s ease-in-out, transform 0.3s 0.1s ease-in-out;transform:scaleY(1)}.nav__list-item{font-size:7vmin;text-transform:uppercase;letter-spacing:0.12em;cursor:pointer;display:block;padding:0.5em;position:relative;background:transparent;color:#fff;outline:none;border:none}.nav__list-item::after{content:'';position:absolute;top:0;left:0;width:2em;height:100%;z-index:-1;background:#a34bed;transition:transform 0.1s linear;transform:scaleX(0);transform-origin:left}.nav__list-item:hover::after,.nav__list-item:focus::after{transform:scaleX(1)}.nav__list-item--active{color:#a34bed}.nav__list-item--active::after{background:#fff}@media (max-width: 450px){.nav{padding:1rem}}

.section{display:flex;flex-direction:column;align-items:center;text-align:center;min-height:100vh;padding:2rem 0}.section__title{text-transform:uppercase;letter-spacing:0.12em;margin-bottom:64px}

.skills-type{width:100%;display:flex;flex-direction:column;align-items:center;margin-bottom:96px}.skills-type__title{margin-bottom:3rem;font-weight:400;opacity:0.7}.skills__list{list-style:none;display:grid;grid-template-columns:repeat(auto-fit, minmax(110px, 1fr));width:80%;grid-gap:32px;gap:32px}.skills__item{display:flex;flex-direction:column;align-items:center}.skills__icon{height:80px;margin-bottom:16px}.skills__name{text-transform:uppercase;font-weight:bold;letter-spacing:0.1em}

